body {
  outline: 1px solid lightgrey;
  margin: 0;
}
.container{
  max-width: 1200px;
  margin: 0 auto;
}
.header_container{
  width: 100%;
  background-color: rgba($color: #676566, $alpha: 0.6);
  position: absolute;
  top: 0;
  z-index: 1;
}
.logo_nav{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  
}
.header_logo{
  font-family: 'Scriptina Regular';
  font-size: 30px;
  color: #fff;
  letter-spacing: 10px;
}
.nav{
  padding: 5px 0;
  text-decoration: none;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  font-family: 'Alegreya Regular';
  letter-spacing: 3px;
  word-spacing: 30px;
}
.nav:nth-child(2n){
  padding: 0;
}
.nav_icons{
  padding: 10px 0 10px 0;
  margin-right: 20px;
}
.nav_icons:nth-child(3){
  margin-right: 0;
}
.intro{
  width: 100%;
  height: 100vh;
  background-image: url('./img/introbread.jpg');
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  text-align: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.dot_shadow{
 width: 100%;
 height: 100%;
 background-image: radial-gradient(circle 457px at center, #564f4c 0%, rgba(86, 83, 76, 0) 100%);
 opacity: 0.9;
 position: absolute;
}
.intro_content{
  z-index: 1;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
}
.intro_line{
  font-family: 'Playfair Display';
  font-size: 92px;
  font-weight: bold;
  color: #fff;
  letter-spacing:10px;
}
.intro_img{
  margin-top: 56px;
}
.intro_line_line{
  max-width: 700px;
  font-family: 'Proxima Nova Regular';
  font-size: 24px;
  font-weight: 400;
  color: #fff;
  letter-spacing: 5px;
  line-height: 36px;
  margin-top: 69px;
}
.our_menu{
  margin-top: 69px;
  padding: 20px 50px;
  border: none;
  color: #ffffff;
  font-family: 'Alegreya';
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 4.2px;
  background-color: rgba($color: #676566, $alpha: 0.6);
  z-index: 1;
}
.border_line{
  box-shadow: 0px 15px 0px 0px #ffffff ;
}
.border_sphere{

  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  top: 95%;
  left: 50%;
  margin-left: -50px;
}
.second_page{
  width: 100%;
  height: 100vh;
  background-image: url('./img/page2background.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
